*{padding:0;margin:0;box-sizing:border-box;font-family:'Lato', 'Arial', sans-serif}html,body{height:100%}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}:root{--d: 700ms;--e: cubic-bezier(0.19, 1, 0.22, 1);--font-sans: 'Rubik', sans-serif;--font-serif: 'Cardo', serif}img{width:100%}.nav-wrapper{width:100%;position:-webkit-sticky;position:sticky;top:0;background-color:#fff}.nav-wrapper .grad-bar{width:100%;height:5px;background:linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);background-size:400% 400%;-webkit-animation:gradbar 15s ease infinite;animation:gradbar 15s ease infinite}.nav-wrapper .navbar{display:grid;grid-template-columns:1fr 3fr;align-items:center;height:60px;overflow:hidden}.nav-wrapper .navbar img{height:60px;width:auto;justify-self:start;margin-left:20px}.nav-wrapper .navbar ul{list-style:none;display:grid;grid-template-columns:repeat(6, 1fr);justify-self:end;align-items:center;text-align:center}.nav-wrapper .navbar ul .nav-item{width:5rem}.nav-wrapper .navbar ul .nav-item a{color:#000;font-size:0.9rem;font-family:'Lato', 'Arial', sans-serif;font-weight:400;text-decoration:none;transition:color 0.3s ease-out}.nav-wrapper .navbar ul .nav-item a:hover{color:#3498db}.nav-wrapper .navbar ul .search-icon{font-size:0.9rem;margin-top:3px;margin-left:15px;transition:color 0.3s ease-out}.nav-wrapper .navbar ul .search-icon:hover{color:#3498db;cursor:pointer}.nav-wrapper .navbar ul .search{transform:translate(-35%);-webkit-transform:translate(-35%);transition:transform 0.7s ease-in-out;color:#3498db}.nav-wrapper .navbar ul .search-input{position:absolute;top:-4px;right:-125px;opacity:0;z-index:-1;transition:opacity 0.6s ease}.nav-wrapper .navbar ul .search-active{opacity:1;z-index:0}.nav-wrapper .navbar ul input{border:0;border-left:1px solid #ccc;border-radius:0;outline:0;padding:5px}.nav-wrapper .navbar .no-search{transform:translate(0);transition:transform 0.7s ease-in-out}.nav-wrapper .navbar .menu-toggle .bar{width:25px;height:3px;background-color:#3f3f3f;margin:5px auto;transition:all 0.3s ease-in-out}.nav-wrapper .navbar .menu-toggle{justify-self:end;margin-right:25px;display:none}.nav-wrapper .navbar .menu-toggle:hover{cursor:pointer}.nav-wrapper .navbar .mobile-menu.is-active .bar:nth-child(2){opacity:0}.nav-wrapper .navbar .mobile-menu.is-active .bar:nth-child(1){transform:translateY(8px) rotate(45deg)}.nav-wrapper .navbar .mobile-menu.is-active .bar:nth-child(3){transform:translateY(-8px) rotate(-45deg)}@-webkit-keyframes gradbar{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradbar{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@media only screen and (max-width: 720px){.navbar ul{display:flex;flex-direction:column;position:fixed;justify-content:start;top:55px;background-color:#fff;width:100%;height:calc(100vh - 55px);transform:translate(-101%);text-align:center;overflow:hidden}.navbar li{padding:15px}.navbar li:first-child{margin-top:50px}.navbar li a{font-size:1rem}.menu-toggle,.bar{display:block;cursor:pointer}.mobile-nav{transform:translate(0%) !important}.search-icon{display:none}.search-input{display:none}}.card{position:relative;display:flex;align-items:flex-end;overflow:hi