nth-child(1531306728370-e2ebd9d7bb99):before{background-image:url(https://images.unsplash.com/photo-1531306728370-e2ebd9d7bb99?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=400&fit=max&ixid=eyJhcHBfaWQiOjE0NTg5fQ)}.card .card-content{position:relative;display:flex;flex-direction:column;align-items:center;width:100%;padding:1rem;transition:transform var(--q) cubic-bezier(0.19, 1, 0.22, 1);transition:transform var(--q) var(--e);z-index:1}.card .card-content>*+*{margin-top:1rem}.card .card-content .card-title{font-size:1.3rem;font-weight:bold;line-height:1.2}.card .card-content .card-text{font-family:'Cardo', serif;font-family:var(--font-serif);font-size:1.125rem;font-style:italic;line-height:1.35}.card .card-content .btn{cursor:pointer;margin-top:1.5rem;padding:0.75rem 1.5rem;font-size:0.65rem;font-weight:bold;letter-spacing:0.025rem;text-transform:uppercase;color:white;background-color:black;border:none}.card .card-content .btn:hover{background-color:#0d0d0d}.card .card-content .btn:focus{outline:1px dashed yellow;outline-offset:3px}@media (hover: hover) and (min-width: 600px){.card:after{transform:translateY(0)}.card-content{transform:translateY(calc(100% - 4.5rem))}.card-content>*:not(.card-title){opacity:0;transform:translateY(1rem);transition:transform 700ms cubic-bezier(0.19, 1, 0.22, 1) opacity 700ms cubic-bezier(0.19, 1, 0.22, 1);transition:transform var(--d) var(--e) opacity var(--d) var(--e)}.card:hover,.card:focus-within{align-items:center}.card:hover:before,.card:focus-within:before{transform:translateY(-4%)}.card:hover:after,.card:focus-within:after{transform:translateY(-50%)}.card:hover .card-content,.card:focus-within .card-content{transform:translateY(0)}.card:hover .card-content>*:not(.title),.card:focus-within .card-content>*:not(.title){opacity:1;transform:translateY(0);transition-delay:calc(700ms / 8);transition-delay:calc(var(--d) / 8)}.card:focus-within:before,.card:focus-within:after,.card:focus-within .card-content,.card:focus-within .c